<template>
  <div class="home">
    <div class="space_top">
      <div @click="reloadPage()">
        <img
          src="../../assets/swt_img/logo_n.png"
          alt=""
          class="space_top_logo"
        />
      </div>
      <div class="space_top_right">
        <img
          src="../../assets/swt_img/right_menu.png"
          alt=""
          class="space_top_right_img2"
          @click="showMean()"
        />
      </div>
    </div>
    <div class="space_back">
      <video
        ref="videoPlayer"
        controls
        :src="videoUrl"
        style="width: 100%; height: 100%; object-fit: fill"
      ></video>
      <div class="space_home_trade" id="tea_introduction">
        <div>
          <img src="../../assets/swt_img/img1.png" alt="" style="width: 100%" />
        </div>
        <br />
        <div>
          <span class="space_home_title"></span>
        </div>
        <div class="tea_section">
          <img class="tea_img" src="../../assets/swt_img/tea.png" alt="" />
          <div>
            <p class="tea_title"></p>
            <p class="tea_detail">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
               
              
              
              
            </p>
          </div>
        </div>
        <!-- <div class="tea_section">
                  <img class="tea_img" src="../../assets/swt_img/tea.png" alt="" />
                  <div>
                    <p class="tea_title"></p>
                    <p class="tea_detail">
                      
                       
                      
                      
                      
                       
                    </p>
                  </div>
                </div> -->
        <!-- <div class="tea_section">
                  <img class="tea_img" src="../../assets/swt_img/tea.png" alt="" />
                  <div>
                    <p class="tea_title"></p>
                    <p class="tea_detail">
                      
                       
                      
                      
                      
                       
                    </p>
                  </div>
                </div> -->
        <br />
        <div id="tea_photo">
          <span class="space_home_title"></span>
          <div class="space_home_banner">
            <div id="swiper" class="container-fuild">
              <div class="swiper-container banner-swiper">
                <div class="swiper-wrapper">
                  <div
                    class="swiper-slide"
                    v-for="(item, index) in localBanner"
                  >
                    <img class="swiper-lazy" :src="item.imgurl" alt="" />
                  </div>
                </div>
                <!--  -->
                <div class="swiper-pagination"></div>
              </div>
            </div>
          </div>
        </div>
        <div id="tea_NFT">
          <span class="space_home_title">NFT</span>
          <p class="project_detail">
            NFT,SWTNFT
            NFT
          </p>
          <img
            src="../../assets/swt_img/tea_nft.png"
            alt=""
            style="width: 100%"
          />
        </div>
      </div>
    </div>
    <template v-if="meanShow">
      <div class="index_pop_content">
        <div class="index_pop_top">
          <div class="index_pop_top_left">
            <img src="../../assets/swt_img/logo_w.png" alt="" />
          </div>
          <div>
            <img
              src="../../assets/swt_img/tea_close.png"
              alt=""
              class="space_mean_img"
              @click="showMean()"
            />
          </div>
        </div>
        <div class="index_pop_main_tab" @click="reloadPage()">
          <div class="index_pop_middle">
            <a href="javascript:;">
              <div class="index_pop_middle_left">
                <span></span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
        <div class="index_pop_main_tab" @click="toNftTrade()">
          <div class="index_pop_middle">
            <a href="javascript:;">
              <div class="index_pop_middle_left">
                <span></span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
        <div class="index_pop_main_tab">
          <div class="index_pop_middle">
            <a href="#tea_introduction" @click="meanShow = false">
              <div class="index_pop_middle_left">
                <span></span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
        <div class="index_pop_main_tab">
          <div class="index_pop_middle">
            <a href="#tea_photo" @click="meanShow = false">
              <div class="index_pop_middle_left">
                <span></span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
        <div class="index_pop_main_tab">
          <div class="index_pop_middle">
            <a href="#tea_NFT" @click="meanShow = false">
              <div class="index_pop_middle_left">
                <span>NFT</span>
              </div>
              <div class="index_pop_middle_right"></div>
              <div class="index_clear"></div>
            </a>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>
<script>
import { WOW } from "wowjs";
import Swiper from "swiper";
export default {
  data() {
    return {
      meanShow: false,
      connect_show: false,
      langShow: false,
      bannerList: [],
      videoUrl: require("../../assets/swt_img/tea_video.mp4"),
      localBanner: [
        {
          imgurl: require("../../assets/swt_img/tea_photo/tea_photo_1.jpg"),
        },
        {
          imgurl: require("../../assets/swt_img/tea_photo/tea_photo_2.jpg"),
        },
        {
          imgurl: require("../../assets/swt_img/tea_photo/tea_photo_4.jpg"),
        },
        {
          imgurl: require("../../assets/swt_img/tea_photo/tea_photo_5.jpg"),
        },
      ],
    };
  },
  created: function () {},
  beforeDestroy() {},
  async mounted() {
    var wow = new WOW({
      boxClass: "wow", // animated element css class (default is wow)
      animateClass: "animated", // animation css class (default is animated)
      offset: 0, // distance to the element when triggering the animation (default is 0)
      mobile: true, // trigger animations on mobile devices (default is true)
      live: true, // act on asynchronously loaded content (default is true)
      callback: function (box) {
        // the callback is fired every time an animation is started
        // the argument that is passed in is the DOM node being animated
      },
      scrollContainer: null, // optional scroll container selector, otherwise use window
    });
    wow.init();
    new Swiper(".banner-swiper", {
      autoplay: true, //
      loop: true,
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      lazy: {
        loadPrevNext: true,
      },
      observer: true,
      observeParents: true,
    });
  },
  methods: {
    toHome() {
      this.$router.push({
        name: "home",
        query: {},
      });
    },
    toNftTrade() {
      this.$router.push({
        name: "project",
        query: {},
      });
    },
    reloadPage() {
      location.reload();
    },
    //
    showMean() {
      if (this.meanShow) {
        this.meanShow = false;
      } else {
        this.meanShow = true;
      }
    },
  },
};
</script>


